"use client";
import Card from "./ui/card"
import { BarChart, Bar, ResponsiveContainer } from "recharts";

const data = [
  { value: 20 },
  { value: 40 },
  { value: 30 },
  { value: 50 },
  { value: 25 },
  { value: 45 },
  { value: 35 },
];

const TotalSavesCard = () => {
  return (
    <Card className="p-4 w-64 shadow-lg rounded-xl border border-gray-200">
      <h3 className="text-sm text-gray-500">Total Saves</h3>
      <p className="text-2xl font-semibold">$6.682</p>
      <ResponsiveContainer width="100%" height={50}>
        <BarChart data={data}>
          <Bar
            dataKey="value"
            fill="#FE5722"
            radius={[10, 10, 0, 0]}
          />
        </BarChart>
      </ResponsiveContainer>
    </Card>
  );
};

export default TotalSavesCard;
